import React, { useEffect } from "react";
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import { useForm } from "react-hook-form";
import * as yup from "yup";
import { yupResolver } from "@hookform/resolvers/yup";
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import Card from 'react-bootstrap/Card';
import { UpdateUser } from '../store/userSlice';
import Container from 'react-bootstrap/esm/Container';

const schema = yup.object().shape({
  email: yup.string().email().required(),
  password: yup.string().required(),
  name: yup.string().required()
});

export default function EditProfile() {
  const dispatch = useDispatch()
  const { register, handleSubmit, formState: { errors }, reset } = useForm({
    resolver: yupResolver(schema),
  });
  const navigate = useNavigate()

  const onSubmitHandler = async (data) => {

    await dispatch(UpdateUser({ ...data, _id: selectedUser._id, token: user.token }))
    navigate(-1)
  };


  const { user, selectedUser } = useSelector(state => state.user)
  useEffect(() => {
    reset({
      name: selectedUser.name,
      email: selectedUser.email,
    })
  }, [reset, selectedUser])

  return <Container className='p-5'>

    <Card className='m-5 p-3'>
      <Card.Body>
        <Card.Header className='mb-4'>
          <h4>Edit Profile</h4>
        </Card.Header>
        <form onSubmit={handleSubmit(onSubmitHandler)}>
          <Form.Group className="mb-3" controlId="formBasicEmail" onSubmit={(e) => {
            e.preventDefault();
          }}>

            <Form.Group className="mb-3" controlId="formBasicPassword">
              <Form.Label>Name:</Form.Label>
              <input {...register("name")} placeholder="name" type="text" required className='ms-5' />
              <p className='text-danger'>{errors.bane?.message}</p>

            </Form.Group>
            <Form.Label>Email address:</Form.Label>
            <input {...register("email")} placeholder="email" type="email" required className='ms-5' />
            <p className='text-danger'>{errors.email?.message}</p>

          </Form.Group>

          <Form.Group className="mb-3" controlId="formBasicPassword">
            <Form.Label>Password:</Form.Label>
            <input {...register("password")} placeholder="password" type="password" required className='ms-5' />
            <p className='text-danger'>{errors.password?.message}</p>

          </Form.Group>

          <Button variant="primary" type="submit">
            Update
          </Button>
        </form>
      </Card.Body>
    </Card>
  </Container>

}

