import React from "react";
import Card from 'react-bootstrap/Card';
import Container from 'react-bootstrap/esm/Container';
import { useDispatch, useSelector } from 'react-redux';
import Button from 'react-bootstrap/Button';
import Row from 'react-bootstrap/Row';
import { useNavigate } from 'react-router-dom';
import { SetSelected } from '../store/userSlice';

export default function Home() {
  const { user } = useSelector(state => state.user)
  const navigate = useNavigate();
  const dispatch = useDispatch();
  return <Container className='p-5'>
    <Card className='m-5 p-3'>
      <Card.Body
      className='m-3 p-3'>
        {user.user.admin && <Button variant="primary" onClick={() => {
          navigate('/list-user')
        }} >List User</Button>
        }
        <Button className='ms-3' onClick={() => {
          dispatch(SetSelected(user.user))
          navigate('/user/edit/' + user.user._id)
        }}>Edit</Button>
        <div className='bg-light mt-3 p-3'>

          <Row><h4 className='align-items-center'>Name:  {user.user.name}</h4></Row>
          <Row><h4 className='align-items-center'>Email:  {user.user.email}</h4></Row>
        </div>
      </Card.Body>
    </Card>
  </Container>

}

