import React from "react";
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import { useForm } from "react-hook-form";
import * as yup from "yup";
import { yupResolver } from "@hookform/resolvers/yup";
import { useDispatch } from 'react-redux';
import { GetLogin } from '../store/userSlice';
import { Link } from 'react-router-dom';
import Card from 'react-bootstrap/Card';
import Container from 'react-bootstrap/esm/Container';

const schema = yup.object().shape({
  email: yup.string().email().required(),
  password: yup.string().required(),
});

export default function Login() {
  const dispatch = useDispatch()
  const { register, handleSubmit, formState: { errors } } = useForm({
    resolver: yupResolver(schema),
  });

  const onSubmitHandler = async (data) => {
    const response = await dispatch(GetLogin(data))
    if (response?.payload?.response?.status !== 200) {
      alert(response.payload.response.data.message)
    }
  };

  return <Container className='p-5'>
    <Card className='m-5 p-3'>
      <Card.Body>
        <Card.Header className='mb-3'><h4>Login</h4><p>Register if not registered.   <Link to={'/signup'}>Signup</Link></p></Card.Header>   <form onSubmit={handleSubmit(onSubmitHandler)}>

          <Form.Group className="mb-3" controlId="formBasicEmail" onSubmit={(e) => {
            e.preventDefault();
          }}>
            <Form.Label>Email address</Form.Label>
            <input {...register("email")} placeholder="email" type="email" required />
            <p className='text-danger'>{errors.email?.message}</p>

          </Form.Group>

          <Form.Group className="mb-3" controlId="formBasicPassword">
            <Form.Label>Password</Form.Label>
            <input {...register("password")} placeholder="password" type="password" required />
            <p className='text-danger'>{errors.password?.message}</p>

          </Form.Group>

          <Button variant="primary" type="submit">
            Login
          </Button>
        </form>
      </Card.Body>
    </Card>
  </Container>
}

